import React, { Component } from 'react';

class Users extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            items: []
        };
        this.getUsersData = this.getUsersData.bind(this);
    }

    componentDidMount() {
        this.getUsersData();
    }

    getUsersData() {
        fetch("https://reqres.in/api/users?page=1")
            .then(res => res.json())
            .then(
                (result) => {
                    console.log(result.data);
                    this.setState({
                        isLoaded: true,
                        items: result.data
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    render() {
        const { error, isLoaded, items } = this.state;
        if (error) {
            return <div>Error: {error.message}</div>;
        } else if (!isLoaded) {
            return (
                <div id="loading">
                    <div id="loading-image">
                        <img src="./images/loader.gif" width="200px" height="150px" />
                    </div>
                </div>
            );
        } else {
            return (
                <div className="container">
                    <h1 className="text-center">Users List</h1>
                    <hr />
                    <div className="row">
                        {items.map(item => (
                            <div key={item.id} className="col-md-2 text-center">
                                <div className="card">
                                    <img src={item.avatar} className="card-img-top" alt={item.first_name + ' ' + item.last_name} />
                                    <div className="card-body">
                                        <h5 className="card-title">{item.first_name + ' ' + item.last_name}</h5>
                                        <p className="card-text">{item.email}</p>
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            );
        }
    }
}

export default Users;